<template>
  <div class="content">
    <el-card>
      <el-form label-width="100px">
        <div v-for="(item, index) in params" :key="index">
          <el-form-item :label="item.type === 202 ? '' : ''">
            <el-radio v-model="item.give" :label="0"></el-radio>
          </el-form-item>
          <el-form-item>
            <el-radio v-model="item.give" :label="1"></el-radio>
          </el-form-item>
          <el-form-item v-if="item.give == 1">
            <div>
              {{item.type === 202 ? '' : ''}}
              <el-radio v-model="item.isRecieveMax" :label="0"></el-radio>
              <el-radio v-model="item.isRecieveMax" :label="1"></el-radio>
              <el-input-number style="width: 100px;" :disabled="item.isRecieveMax == 1 ? false : true" v-model="item.recieveMaxNum" :precision="0" :min="0" controls-position="right"></el-input-number>
              
            </div>
          </el-form-item>
        </div>
      </el-form>
    </el-card>
  </div>
</template>
<script>
export default {
  props: ['params'],
  data() {
    return {
      
    }
  },
  mounted() {
    
  }
}
</script>



