<template>
  <div class="container">
    <el-card>
       <el-form ref="prizeConfRef" :model="form" label-width="100px" :rules="confRules">
          <el-form-item label="" prop="name">
            <el-input v-model="form.name" :maxlength="15"></el-input>
          </el-form-item>
          <el-form-item label="" prop="subType" v-if="params.type == 2">
            <el-select v-model="form.subType" placeholder="">
              <el-option label="" :value="202"></el-option>
              <el-option label="" :value="201"></el-option> 
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="icon">
            <el-upload class="avatar-uploader" :before-upload="beforeAvatarUpload" :action="uploadURL" :headers="headerObj" :on-success="upBannerImg" :on-error="upBannerImgErr" :show-file-list="false">
              <img v-if="form.icon" :src="form.icon" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              <div slot="tip" class="el-upload__tip">360*270pngjpg2M</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="" prop="value" v-if="params.type == 2 && form.subType == 202">
            <el-input-number controls-position="right" :min="1" :max="10" :precision="0" v-model="form.value"></el-input-number> 
            <!-- <el-input style="width: 100px;" v-model="form.value"></el-input>  -->
          </el-form-item>
          <el-form-item label="" prop="value" v-if="params.type == 2 && form.subType == 201">
            <el-input-number controls-position="right" :min="0.00" :max="1.00" :precision="2" :step="0.1" v-model="form.value"></el-input-number> 
          </el-form-item>
          <el-form-item label="" prop="value" v-if="params.type == 3">
            <el-input-number controls-position="right" :min="0.30" :precision="2" v-model="form.value"></el-input-number> 
          </el-form-item>
          <el-form-item label="" prop="value" v-if="params.type == 6">
            <el-input-number controls-position="right" :min="0" :precision="0" v-model="form.value"></el-input-number> 
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="form.note" :rows="3" resize="none"></el-input>
          </el-form-item>
          <el-form-item class="mt20">
            <el-button type="primary" @click="save"></el-button>
            <el-button plain @click="goBack"></el-button>
          </el-form-item>
       </el-form>
    </el-card>
  </div>
</template>
<script>
export default {
  // props: ['type', 'detailId'],
  props: ['params'],
  data() {
    var validateBanner = (rule, value, callback) => {
      if (!this.form.icon) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      form: {
        id: this.params.id,
        type: this.params.type,
        subType: null,
        name: '',
        icon: '',
        value: 0,
        note: ''
      },
      confRules: {
        subType: [{ required: true, message: '', trigger: 'blur' }],
        name: [{ required: true, message: '', trigger: 'blur' }],
        value: [{ required: true, message: '', trigger: 'blur' }],
        icon: [{ required: true, validator: validateBanner }]
      },
      uploadURL: '/api/saotx/attach/commonAliUpload',
      headerObj: {
        loginId: sessionStorage.getItem('access_loginId'),
        token: sessionStorage.getItem('access_token'),
        CLIENTSESSIONID: sessionStorage.getItem('CLIENTSESSIONID')
      },
      detailData: {},
      typeName: ''
    }
  },
  created () {
    this.prizeDetail()
    // this.getTypeName()
  },
  methods: {
    prizeDetail() {
      if (this.params.id) {
        this.$request.post('/api/saotx/md/detailMaterial', { id: this.params.id }, true, res => {
          if (res.ret === '200000') {
            this.form = res.data
          } else {
            this.$message.error(res.message)
          }
        })
      }
    },
    getTypeName() {
      let arr = [
        {type: 1, name: ''},
        {type: 2, name: ''},
        {type: 3, name: ''},
        {type: 6, name: ''},
      ]
    },
    save() {
      if (this.params.type != 2) {
        this.form.subType = ''
      }
      this.$refs.prizeConfRef.validate(valid => {
        if (!valid) return this.$message.error('!')
        this.$request.post('/api/saotx/md/somMaterial', this.form, true, res => {
          if (res.ret === '200000') {
            if (this.params.id) {
              this.$message.success('')
            } else {
              this.$message.success('')
            }
            this.$router.push('/memberday/material/prizeList?type=' + this.params.type)
          } else {
            this.$message.error(res.message)
          }
        })
      })
    },
    goBack() {
      this.$router.push('/memberday/material/prizeList?type=' + this.params.type)
    },
    upBannerImg(resule) {
      if (resule.ret === '200000') {
        this.form.icon = resule.data.accessUrl
        this.$refs.prizeConfRef.validateField('icon', valid => {})
        return
      }
      this.$message.error(resule.message)
    },
    upBannerImgErr(err) {
      this.$message.error(', !')
    },
    beforeAvatarUpload(file) {
      const JPGOrPNG = file.type === 'image/jpeg' || file.type === 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!JPGOrPNG) {
        this.$message.error(' JPG  PNG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return JPGOrPNG && isLt2M;
    }
  }
}
</script>
<style lang="scss" scoped>
.el-form {
  width: 500px;
  margin: 20px
}
.el-input {
  width: 300px;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 150px;
  height: 108px;
  line-height: 108px;
  text-align: center;
}
.avatar {
  width: 150px;
  height: 108px;
  display: block;
}
.quill-editor {
  width: 420px;
}
</style>



