<template>
  <div class="container">
    <!--  -->
    <el-dialog :visible.sync="openDialog" width="400px" top="35vh" :close-on-click-modal="false" @close="reset">
      <h3></h3>
      <el-form label-width="100px">
        <el-form-item label="">
          <el-input v-model="params.logisticalCode" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="params.logisticalCompany" placeholder=""></el-input>
        </el-form-item>
        <div class="btn">
          <el-button type="primary" @click="confirm"></el-button>
          <el-button plain @click="openDialog = false"></el-button>
        </div>
      </el-form>
    </el-dialog>
    <!--  -->
    <div v-if="params.status == 2">
      <!-- <span></span> -->
      <el-form class="mt20" :model="params" label-width="100px">
        <!-- <el-form-item label="">
          <span class="mr20" v-if="params.sendTime">{{new Date(params.sendTime).Format('yyyy-MM-dd hh:mm:ss')}}</span>
          <span v-if="params.status == 2"></span>
        </el-form-item> -->
        <el-form-item label="">
          <span v-if="!params.img.img"></span>
          <div v-else>
            <img class="img" :title="scaleStyle ? '' : ''" :style="scaleStyle" @click="restore" :src="params.img.img">
            <el-button type="primary" @click="scaleUp"></el-button>
          </div>
        </el-form-item>
        <div v-if="params.img.img">
          <el-form-item label="">
            <span>{{statusList[params.img.authStatus]}}</span>
          </el-form-item>
          <el-form-item label="" v-if="params.img.authStatus == 11">
            <el-radio v-model="saveParams.authStatus" :label="12"></el-radio>
            <el-radio v-model="saveParams.authStatus" :label="14"></el-radio>
            <el-input v-model="saveParams.authDesc" :disabled="saveParams.authStatus == 12 ? true : false" style="display: inline-block; width: 230px; margin-left: 10px;" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" v-if="params.img.authStatus == 14">
            <span style="color: #FF0000">{{params.authDesc}}</span>
          </el-form-item>
        </div>
        <div class="btn">
          <el-button type="primary" v-if="params.img.img && params.img.authStatus == 11" @click="save"></el-button>
          <el-button plain @click="$router.go(-1)"></el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  props: ['openDialog', 'params'],
  data() {
    return {
      deliverDialogVisible: false,
      orderDialogVisible: false,
      radio: '',
      scaleStyle: null,
      statusList: {
        11: '',
        12: '',
        14: ''
      },
      saveParams: {
        orderCode: this.params.orderCode,
        authStatus: 12,
        authDesc: ''
      }
    }
  },
  created () {
    // if (this.params.status === 2) return
    // this.deliverDialogVisible = true
  },
  methods: {
    confirm() {
      if (!this.params.province && !this.params.city && !this.params.detail) return this.$message.error('')
      if (!this.params.logisticalCompany || !this.params.logisticalCode) return this.$message.error('')
      this.$request.post('/api/saotx/md/modifyOrder', this.params, true, res => {
        if (res.ret === '200000') {
          this.$message.success('')
          this.deliverDialogVisible = false
          this.$emit('refresh')
          return
        }
        this.$message.error(res.message)
      })
    },
    scaleUp() {
      this.scaleStyle = {
        'position': 'fixed',
        'top': '50%',
        'left': '50%',
        'transform': 'translate(-50%, -50%) scale(5)',
        'display': 'block',
        'z-index': '9999999',
        'cursor': 'pointer'
      }
    },
    restore() {
      this.scaleStyle = null
    },
    save() {
      if (this.saveParams.authStatus == 14 && !this.saveParams.authDesc) return this.$message.error('!')
      this.$request.post('/api/saotx/md/authOrder', this.saveParams, true, res => {
        if (res.ret === '200000') {
          this.$message.success('!')
          // this.orderDialogVisible = false
          this.$emit('refresh')
          return
        }
        this.$message.error(res.message)
      })
    },
    reset() {
      this.$emit('taggle')
    }
  }
}
</script>
<style lang="scss" scoped>
h3 {
  text-align: center;
  margin: 0 0 20px 0;
}
.el-dialog /deep/ .el-dialog__header {
  padding: 0;
}
.btn {
  padding-left: 200px;
  text-align: left;
}
.img {
  vertical-align: bottom;
  max-width: 200px;
  // max-height: 150px;
  // transition: all 1s;
  // transform: rotate(90deg);
}

</style>



