<template>
  <div class="content">
    <el-table border :v-loading="loading" :stripe="true" :data="awardList" tooltip-effect="dark" style="width: 100%">
      <el-table-column align="center" prop="orderCode" label=""></el-table-column>
      <el-table-column align="center" prop="activityName" label=""></el-table-column>
      <el-table-column align="center" prop="prizeWinner" label=""></el-table-column>
      <el-table-column align="center" prop="mobile" label=""></el-table-column>
      <el-table-column align="center" prop="awardType" label="">
        <template slot-scope="scope">
          <span>{{prizeType[scope.row.awardType]}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="awardName" label=""></el-table-column>
      <el-table-column align="center" prop="term" label="">
        <template slot-scope="scope">
          {{new Date(scope.row.ctime).Format('yyyy-MM-dd hh:mm:ss')}}
        </template>
      </el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <span>{{scope.row.awardProvince}}{{scope.row.awardCity ? '-' + scope.row.awardCity : ''}}{{scope.row.awardDistrict ? '-' + scope.row.awardDistrict : ''}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="orderStatus" label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope"><el-button type="text" v-if="scope.row.awardType == 1 && scope.row.status != 1" @click="goDetailPage(scope.row.orderCode)"></el-button></template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  props: ['awardList', 'loading'],
  data() {
    return {
      prizeType: {
        '1': '',
        '201': '',
        '202': '',
        '3': '',
        '6': ''
      },
    }
  },
  methods: {
    goDetailPage(code) {
      this.$router.push('/memberday/order/commonAwardDetail?orderCode=' + code)
    }
  }
}
</script>



